.choice:hover {
  opacity: 0.8;
}

.choice {
  animation: initialshow 3s linear;
}

@keyframes initialshow {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.icon:hover {
  opacity: 0.8;
}

.modelanswer {
  cursor: pointer;
}

body {
  padding: 0;
  margin: 0;
}

.modelQuestion {
  animation: canvasShow 5s;
  margin-top: 200px;
}

@keyframes canvasShow {
  from {
    margin-top: 500px;
  }
  to {
    margin-top: 200px;
  }
}
